.bubble {
  position: relative;
  display: inline-block;
  overflow: hidden;
  color: #ffffff;
  font-family: "ARS", Arial, sans-serif;
  font-size: 22px;
  line-height: 26px;
  border-radius: 25%/50%;
}

@media (max-width: 999px) {
  .bubble {
    font-size: 16px;
    line-height: 22px;
  }
}

@media (max-width: 639px) {
  .bubble {
    font-size: 15px;
    line-height: 20px;
  }
}

@media (max-width: 399px) {
  .bubble {
    font-size: 13px;
    line-height: 16px;
  }
}

.bubble--less-rounded {
  border-radius: 25%;
}

.bubble--elevated {
  box-shadow: 7px 7px 20px 0 rgba(0, 0, 0, 0.1);
}

.bubble--color-orange {
  color: #fd5300;
}

.bubble--picto {
  -ms-flex-direction: column;
  flex-direction: column;
  -ms-flex-pack: center;
  justify-content: center;
  display: -ms-flexbox;
  display: flex;
  text-align: center;
  border-radius: 25%;
  border-top-left-radius: 0 !important;
}

.bubble--picto .bubble__container {
  padding: 35px 30px;
}

.bubble--picto svg {
  display: block;
  width: 90px;
  height: auto;
  margin: 0 auto 25px;
}

.bubble--bg-orange {
  background-color: #fd5300;
  border-top-left-radius: 0 !important;
}

.bubble--bg-orange.bubble--transparent::after {
  background-color: #fd5300;
}

.bubble--bg-white {
  color: #fd5300;
  background-color: #ffffff;
}

.bubble--bg-white.bubble--transparent::after {
  background-color: #ffffff;
}

.bubble--bg-blue {
  background-color: #009db8;
  border-top-right-radius: 0 !important;
}

.bubble--bg-blue.bubble--transparent::after {
  background-color: #009db8;
}

.bubble--transparent {
  background-color: transparent;
  /*@supports (mix-blend-mode: multiply){
            &::before{
                content:"";
                z-index: 0;
                position: absolute;
                top:0;
                left: 0;
                width:100%;
                height:100%;
                background-color: rgba($white, 0.75);
            }

            &::after{
                opacity: 1;
                mix-blend-mode: multiply;
            }
        }*/
}

.bubble--transparent::after {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  width: 100%;
  height: 100%;
  content: "";
  opacity: 0.8;
}

.bubble--border-orange {
  border: 3px solid #fd5300;
}

.bubble--border-white {
  border: 3px solid #ffffff;
}

.bubble--corner-left {
  border-top-left-radius: 0 !important;
}

.bubble--corner-right {
  border-top-right-radius: 0 !important;
}

.bubble--bbloc .bubble__container, .bubble--bbloc-orange .bubble__container {
  padding-top: calc(28px/2 + 28px + 28px/2);
  padding-left: calc(40px/2 + 28px/2);
}

.bubble--bbloc .bubble__container::before, .bubble--bbloc-orange .bubble__container::before {
  position: absolute;
  top: 12px;
  left: 12px;
  width: 40px;
  height: 30px;
  content: "";
  background-image: url("../svg/b-bloc.svg");
  background-repeat: no-repeat;
  background-size: 100% auto;
}

@media (max-width: 639px) {
  .bubble--bbloc .bubble__container, .bubble--bbloc-orange .bubble__container {
    padding-top: 45px;
    padding-bottom: 20px;
    padding-left: 30px;
  }
  .bubble--bbloc .bubble__container::before, .bubble--bbloc-orange .bubble__container::before {
    width: 32px;
  }
}

@media (max-width: 399px) {
  .bubble--bbloc .bubble__container, .bubble--bbloc-orange .bubble__container {
    padding-top: 36px;
  }
  .bubble--bbloc .bubble__container::before, .bubble--bbloc-orange .bubble__container::before {
    width: 26px;
  }
}

.bubble--bbloc.bubble--loading .bubble__loader, .bubble--bbloc-orange.bubble--loading .bubble__loader {
  opacity: 1;
  visibility: visible;
}

.bubble--bbloc-orange .bubble__container::before {
  background-image: url("../svg/b-bloc_orange.svg");
}

.bubble__container {
  position: relative;
  z-index: 10;
  padding: 35px 30px 30px 35px;
}

@media (max-width: 639px) {
  .bubble__container {
    padding: 25px 30px;
  }
}

@media (max-width: 399px) {
  .bubble__container {
    padding: 14px 27px 16px 18px;
  }
}

.conversation__root {
  position: relative;
  -ms-flex-align: end;
      align-items: flex-end;
  -ms-flex-direction: column;
      flex-direction: column;
  display: -ms-flexbox;
  display: flex;
}

.conversation__root .bubble--bg-orange,
.conversation__root .bubble--align-left {
  -ms-flex-item-align: start;
      align-self: flex-start;
}

.conversation__root .bubble + .bubble {
  margin-top: 30px;
}

@media (max-width: 639px) {
  .conversation__root .bubble + .bubble {
    margin-top: 20px;
  }
}

@media (max-width: 399px) {
  .conversation__root .bubble + .bubble {
    margin-top: 14px;
  }
}

.conversation--animate .bubble, .conversation--animate-simple .bubble {
  opacity: 0;
}

.conversation__textarea {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 5px 10px;
  font-size: 15px;
  background-color: #ffffff;
  border: 5px solid #eaeaea;
}

.conversation2 {
  margin: 70px 0;
  background-size: cover;
}

.conversation2 .conversation {
  padding: 5% 0;
  border: 0 none;
}

@keyframes loading {
  0% {
    top: 0;
  }
  30% {
    top: -8px;
  }
  60% {
    top: 0;
  }
}

.bubble__loader {
  position: absolute;
  top: 26px;
  left: 80px;
  display: block;
  transition: visibility 0.3s, opacity 0.3s;
  opacity: 0;
  visibility: hidden;
}

.bubble__loader span {
  position: relative;
  display: block;
  width: 8px;
  height: 8px;
  background-color: #ffffff;
  border-radius: 50%;
  animation-delay: 0.25s;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-name: loading;
  animation-timing-function: ease-out;
}

.bubble__loader::before {
  position: absolute;
  left: -16px;
  width: 8px;
  height: 8px;
  content: "";
  background-color: #ffffff;
  border-radius: 50%;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-name: loading;
  animation-timing-function: ease-out;
}

.bubble__loader::after {
  position: absolute;
  top: 0;
  left: 16px;
  width: 8px;
  height: 8px;
  margin-right: 10px;
  content: "";
  background-color: #ffffff;
  border-radius: 50%;
  animation-delay: 0.5s;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-name: loading;
  animation-timing-function: ease-out;
}
